  private async fetchCvv(partner_user_id: string, programId: string): Promise<string> {
    const getCvvUrl =
        CONFIG.SERVER_URL + CONFIG.GET_CVV
          + `?partnerUserId=${encodeURIComponent(partner_user_id)}&programId=${encodeURIComponent(programId)}`;
    try {
      const resp = await fetch(getCvvUrl, {
        method: 'GET',
        headers: {
          'Accept': 'application/json'
        },
        cache: 'no-store'
      });
      console.log('RebateSDK: fetch CVV to', getCvvUrl, 'status', resp.status);
      if (resp.ok) {
        const data = await resp.json();
        console.log('RebateSDK: CVV response', data);
        const decryptedCvv = await this.decrypt(data.cvv);
        return decryptedCvv as string;
      } else {
        console.warn('RebateSDK: CVV fetch returned non-ok status', resp.status);
      }
    } catch (err) {
      console.warn("RebateSDK: failed to fetch CVV", err);
    }
    return '123'; // Fallback CVV
  }

  private async decrypt(encryptedData: string): Promise<string> {
    const sharedSecret = "a591a6d40bf420404a011733cfb7b190d62c65bf0bcda32b57b277d9ad9f146e";

    try {
      const encryptedBuffer = Uint8Array.from(atob(encryptedData), c => c.charCodeAt(0));

      const ivSize = 16;
      const iv = encryptedBuffer.slice(0, ivSize);

      const encrypted = encryptedBuffer.slice(ivSize);

      const encoder = new TextEncoder();
      const secretBuffer = encoder.encode(sharedSecret);
      const hashBuffer = await crypto.subtle.digest('SHA-256', secretBuffer);
      const key = new Uint8Array(hashBuffer).slice(0, 32); // Take first 32 bytes

      const cryptoKey = await crypto.subtle.importKey(
          'raw',
          key,
          { name: 'AES-CBC' },
          false,
          ['decrypt']
      );

      const decryptedBuffer = await crypto.subtle.decrypt(
          { name: 'AES-CBC', iv: iv },
          cryptoKey,
          encrypted
      );

      const decoder = new TextDecoder();
      return decoder.decode(decryptedBuffer);
    } catch (err: any) {
      throw new Error('Decryption failed: ' + err.message);
    }
  }

  private maskCardNumber(cardNumber: string): string {
    // Extract last 4 digits
    console.log(cardNumber)
    const last4 = cardNumber.replace(/\s/g, '').slice(-4);
    return `•••• •••• •••• ${last4}`;
  }

