    const tabsIcon = `<svg width="28" height="28" viewBox="0 0 28 28" fill="none"><rect x="7" y="7" width="14" height="10" rx="1.5" stroke="#007AFF" stroke-width="2"/><path d="M21 12h-3m0 5h3" stroke="#007AFF" stroke-width="2" stroke-linecap="round"/></svg>`;
    
